:local(.questions) {
  line-height: 1.4em;
  font-size: 1rem;
  background: #FFF; }
  .mq-desktop :local(.questions) {
    width: 80%;
    margin: 10%;
    border-radius: 20px;
    -webkit-box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
            box-shadow: 0px 0px 20px 0px rgba(0, 0, 0, 0.1);
