@-webkit-keyframes countdown {
  0% {
    background-position: right bottom; }
  100% {
    background-position: left bottom; } }

@-webkit-keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);
            box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7); }
  80% {
    -webkit-box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);
            box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7); }
  100% {
    -webkit-box-shadow: 0 0 0 15px rgba(125, 159, 137, 0);
            box-shadow: 0 0 0 15px rgba(125, 159, 137, 0); } }

@keyframes pulse {
  0% {
    -webkit-box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);
            box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7); }
  80% {
    -webkit-box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7);
            box-shadow: 0 0 0 0 rgba(125, 159, 137, 0.7); }
  100% {
    -webkit-box-shadow: 0 0 0 15px rgba(125, 159, 137, 0);
            box-shadow: 0 0 0 15px rgba(125, 159, 137, 0); } }



